<template>
  <!-- <button @click="$store.commit('guan')"> x</button> -->
  <div id=" login" class="content-page" v-show="$store.state.isdenglushow">
    <div style="
        max-width: 450px; 
        margin: 0 auto;
        background: white;
        border-radius: 16px;
        box-shadow: var(--card-shadow);
        overflow: hidden;
        width: 395px;
      ">
      <div style="background: var(--primary-color); color: white; text-align: center; padding: 30px">
        <h2 style="font-size: 28px; margin-bottom: 10px">欢迎回来</h2>
        <p>志愿社区管家平台</p>
      </div>
      <div style="padding: 30px">
        <div class="form-group">
          <label for="login-username">用户名</label>
          <input type="text" id="login-username" class="form-control" placeholder="请输入邮箱" name="email" v-model="name"
            @input="jiancename" />
          <span class="yonghumingcuowu">{{ yonghuname }}</span>
        </div>
        <div class="form-group">
          <label for="login-password">密码</label>
          <input type="password" id="login-password" class="form-control" placeholder="请输入密码" name="Password"
            v-model="password" @input="jiancepassword" />
          <span class="yonghumingcuowu">{{ yonghupassword }}</span>
        </div>
        <div style="
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 20px;
          ">
          <div>
            <input type="checkbox" id="remember" v-model="tongyi" @click="iftongyi" />
            <label for="remember" style="margin-left: 5px ;color: var(--primary-color)">同意协议</label>
          </div>
          <!-- <div>
            <input type="checkbox" id="remember" v-model="ifjizhume" @click="jizhume" />
            <label for="remember" style="margin-left: 5px">记住我</label>
          </div> -->
          <a href="#" style="color: var(--primary-color); text-decoration: none" @click="wnagijpassdorw">忘记密码?</a>
        </div>

        <button :disabled="isDisabled" class="btn btn-primary btn-block" id="loginBtn" type="submit" @click="denglu">
          {{ annuvalue }}
        </button>

        <div style="
            display: flex;
           justify-content: center;
            align-items: center;
            margin-top: 20px;
          ">
        </div>
        <div style="
            text-align: center;
            margin-top: 25px;
            padding-top: 20px;
            border-top: 1px solid var(--border-color);
          ">
          <p>
            还没有账号?
            <a href="#" id="goToRegister"
              style="padding: 20px 20px; color: var(--primary-color); text-decoration: none; font-weight: 500"
              @click="lijizhuce">立即注册</a>
          </p>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
// import api from "@/store/axiosqingqiu";
import qingqiu from "@/store/AjaxRequest";
// import { mapState } from "vuex";

export default {
  data() {
    return {
      //name
      name: "",
      yonghuname: "",

      //password
      password: "",
      yonghupassword: "",

      //记住我
      ifjizhume: false,

      //同意协议
      tongyi: false,

      //用户信息
      userarr: "",

      //登录错误
      dengluname: '',
      denglupassword: "",

      //按钮
      isDisabled: false,
      annuvalue: "登录"
    };
  },
  created() {

  },
  methods: {
    //输入用户名
    jiancename() {
      //name
      if (!/^[a-zA-Z0-9\u4e00-\u9fa5._-]+@[a-zA-Z0-9.-]+\.[a-zA-Z]{2,}$/.test(this.name)) {
        this.yonghuname = "电子邮件格式不正确"
        return;
      }
      if (this.name == "") {
        (this.yonghuname = "电子邮件不能为空")
        return;
      }
      if (/\s/.test(this.name)) {
        (this.yonghuname = "电子邮件不能有空格")
        return;
      }
      if (this.name.length > 64) {
        this.yonghuname = "电子邮件不应超过64个字符";
        return;
      }
      this.yonghuname = ""
    },
    //输入密码
    jiancepassword() {
      /^.{8,16}$/.test(this.password)
        ? this.yonghupassword = ""
        : this.password === ""
          ? (this.yonghupassword = "密码不能为空")
          : (this.yonghupassword = "密码应为8~16个字符");
    },
    //记住我
    jizhume() {
      this.ifjizhume = !this.ifjizhume
      if (this.ifjizhume) {
        this.$store.commit("ifjizhuwo")
        console.log(this.$store.state.jizhuwo)
      }
    },
    //同意协议
    iftongyi() {
      this.tongyi = !this.tongyi
    },
    //忘记密码
    wnagijpassdorw() {
      alert("你真的忘记密码了？");
      alert("你个sb");
    },
    //注册
    lijizhuce() {
      this.$router.push("UserRegistration");
    },
    //登录
    denglu() {
      this.isDisabled = true;
      this.annuvalue = "登录中..."
      // 请求数据
      const chuanxinxi = new URLSearchParams();
      // chuanxinxi.append("email", "hm2897135735@qq.com");
      // chuanxinxi.append("password", "88888888");
      chuanxinxi.append("email", this.name);
      chuanxinxi.append("password", this.password);

      const jsonData = chuanxinxi.toString().replace(/%40/g, '@');

      const shuju1 = { "pattern": "POST", "url": "http://8.148.31.153:3030/login", "shuju": jsonData }
      qingqiu(shuju1, (qinqiushuju) => {
        // console.log(qinqiushuju)
        this.userarr = JSON.parse(qinqiushuju.responseText).success == false ? JSON.parse(qinqiushuju.responseText).message : JSON.parse(qinqiushuju.responseText)
        // console.log(JSON.parse(qinqiushuju.responseText))
        if (JSON.parse(qinqiushuju.responseText).success) {
          //数据正确
          if (this.tongyi) {
            //同意
            //本地存储
            localStorage.setItem("denglutoken", true)
            //改文字
            this.$store.commit("dlcg", "退出登录")
            //登录成功的用户信息
            this.$store.commit("yonghuxx", this.userarr)
            localStorage.setItem("userxingxi", JSON.stringify(this.userarr));
            const times1 = setTimeout(() => { this.$router.push({ name: "FunctiontWorkbench" }), this.$store.commit("ifisgongzuotaishow", true) }, 2000)
            times1
            this.isDisabled = false
          } else {
            //不同意  
            this.annuvalue = "登录"
            this.$store.commit("tanchaung", "同意协议方可登录")
            this.$store.commit("tanchuangshow", true)
            this.tongyi = true;
            this.isDisabled = false
          }
        } else {
          //数据错误
          this.$store.commit("Cuowu", this.name == '' && this.password == "" ? "账号或密码不能为空" : this.userarr)
          this.$store.commit("Cuowushow", true)
          this.annuvalue = "登录"
          this.isDisabled = false
        }
      },
        false)

      //使用axios请求
      // api({
      //   url: "http://8.148.70.85:3030/login",
      //   method: "POST",
      //   headers: {
      //     "Content-Type": "application/x-www-form-urlencoded",
      //     "Accept": "application/json"
      //   },
      //   data: new URLSearchParams({ // 关键修复：使用 URLSearchParams 编码数据
      //     username: "0001",
      //     password: "root1212"
      //   }).toString()
      // }).then(rel => {
      //   console.log(rel);
      // }).catch(error => {
      //   console.error("请求失败：", error);
      // });
    },
  },
};
</script>
<style scoped>
:root {
  --primary-color: #3498db;
  --primarys-hover: #358ac3;
  --border-color: #e0e0e0;
  --text-color: #333;
  --background-light: #f8f9fa;
  --card-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  --transition: all 0.3s ease;
}

.yonghumingcuowu {
  color: red;
}

.content-page {
  width: 100%;
  margin: 0 auto;
}

.register-container {
  max-width: 800px;
  margin: 0 auto;
  background: white;
  border-radius: 16px;
  box-shadow: var(--card-shadow);
  overflow: hidden;
  width: 100%;
}

.page-header {
  background: var(--primary-color);
  color: white;
  text-align: center;
  padding: 30px;
}

.page-header h2 {
  font-size: 28px;
  margin-bottom: 10px;
  font-weight: 600;
}

.page-header p {
  font-size: 16px;
  opacity: 0.9;
}

.form-content {
  padding: 30px;
}

.form-row {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-bottom: 20px;
}

.form-col {
  flex: 1;
  min-width: 250px;
}

.form-group {
  margin-bottom: 5px;
  position: relative;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  font-weight: 500;
  font-size: 15px;
  color: #555;
}

.form-control {
  width: 100%;
  padding: 12px 15px;
  border: 1px solid var(--border-color);
  border-radius: 8px;
  font-size: 15px;
  transition: var(--transition);
}

.form-control:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px rgba(52, 152, 219, 0.2);
}

.verification-group {
  display: flex;
  gap: 10px;
}

.verification-group .form-control {
  flex: 1;
}

.verification-code-btn {
  background-color: #f8f9fa;
  border: 1px solid var(--border-color);
  border-radius: 8px;
  padding: 0 15px;
  cursor: pointer;
  color: var(--primary-color);
  font-weight: 500;
  white-space: nowrap;
  transition: var(--transition);
}

.verification-code-btn:hover {
  background-color: #e3f2fd;
}

.agreement-group {
  margin: 13px 0;
  display: flex;
  align-items: flex-start;
}

.agreement-group input {
  margin-top: 3px;
  margin-right: 8px;
}

.agreement-group a {
  color: var(--primary-color);
  text-decoration: none;
}

.agreement-group a:hover {
  text-decoration: underline;
}

.btn {
  display: block;
  width: 100%;
  padding: 13px 20px;
  background-color: var(--primary-color);
  color: white;
  font-size: 16px;
  font-weight: 500;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: var(--transition);
}

.btn:hover {
  background-color: rgb(30, 136, 229, 0.7);
  transform: translateY(-2px);
}

.btn:active {
  transform: translateY(0);
}

.login-link {
  text-align: center;
  margin-top: 25px;
  padding-top: 20px;
  border-top: 1px solid var(--border-color);
  font-size: 15px;
}

.login-link a {
  color: var(--primary-color);
  text-decoration: none;
  font-weight: 500;
  margin-left: 5px;
}
</style>